body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(#97d8d2,#4ea6e4,#185b9a);
  width: 100vw;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Auth{
  width: 30vw;
  height: 40vh;
  background-color: #97d8d2;
  border: 2px black solid;
  position: absolute;
  top:20vh;
  left:35vw;
}
.nav{  
  
  list-style: none;
}
.nav li{
  display: inline;
  margin-left: 1vw;
}
.nav-item{
  text-decoration: none;
  color: black;
  font-size: large;
  font-weight: bold;  
}
.login{
  margin-left: 5vw;
  margin-top: 5vh;
  width: 20vw;
  align-items: center;
}
.form{
  margin-left: auto;
}
.wrong{
  color:red;
  border: 2px red solid;
}
#massage{
  margin-top: 1vh;
}
.chat{
  background-color: #97d8d2;
  border: 2px solid black;
  box-shadow:5px 5px 50px 10px #41dbf6;
  position: absolute;
  top: 5vh;
  left: 15vw;
  width: 70vw;
  height: 90vh;
  padding: 0;
}
.header{
  text-align:center;
  font-weight:bold;
  font-size:2rem;
  border-bottom: 1px black solid;
  background-color:#41dbf6;
  letter-spacing: 1rem;  
}
.chatsBar{
  margin: 0;
  min-height: 84vh;
  width: 15vw;  
}
.newChat{
  margin-top: 5px;
  margin-left: 5px;
}
.newChat-button{
  margin-left: 5px;
  border-radius: 50%;
  
}
.newChat-button:active, .newChat-button:focus {
  outline: none;
}
.searchUser{
  border-radius: 10px;
  outline: none;
}
.avatar{
  font-weight: bold;
  width: 50px;
  height: 50px;
  background-color: #f6eaa7;  
  border-radius: 50%;
  text-align: center;
  line-height:50px;
  border: 1px black solid;
}
.chatItem{
  margin-top: 20px;
  margin-left: 7px;
  width: 100%;
  background-color:#cdf9f3;
  border: 1px black solid;
  height: 70px;
  padding-top: 7px;
  padding-left: 10px;
  display: flex; 
}
.chatItem-massage{
  margin-top: 10px;
  margin-left: 5px;
  text-decoration: none;
  color:black
}
.massage-content{
  position: absolute;
  top: 6vh;
  right: 20px;
  width: 70%;
  height: 80%;
  padding-bottom: 50px;
  background-color:#cdf9f3 ;
  border: 2px black solid;
  overflow:auto;
}
.massage-item{
  padding: 10px 10px 10px 10px;
  background-color: #41dbf6;
  border: 1px solid black;
  width: 13vw;  
  height: auto;
  margin-top: 40px;  
  display: flex;
}
.massage-item-content{  

  text-align: end;
  margin-right:10px;
  width:150px;
}
.massage-item-left{
  height: 50px;
  line-height: auto;  
  text-align: start;
  margin-left: 10px;
  width:150px;  
}
.right{  
  margin-left: 70%;
}
.left{
  margin-left: 20px;
}
.massage{  
  display: flex;
  justify-content: space-evenly;
  width: 25vw;
  position: absolute;
  top: 79vh;
  left: 45vw;
  outline: none;
}
.button{
  margin-left: 10px;
  padding: 0;
  width: 6vw;
  height: 4vh;
}

  

.example-2 .btn-tertiary{color:#555;padding:0;line-height:40px;width:50px;margin:auto;display:block;border:2px solid #555}
.example-2 .btn-tertiary:hover,.example-2 .btn-tertiary:focus{color:#888;border-color:#888}
.example-2 .input-file{width:.1px;height:.1px;opacity:0;overflow:hidden;position:absolute;z-index:-1}
.example-2 .input-file + .js-labelFile{overflow:hidden;text-overflow:ellipsis;white-space:nowrap;padding:0 10px;cursor:pointer}
.example-2 .input-file + .js-labelFile.has-file .icon:before{content:"\f00c";color:#5AAC7B}
